import React, { useEffect, useRef, useState } from "react";
import PictureFuture from "../images/picture-border.webp";
import "../SectionQuiJeSuis/SectionQuiJeSuis.scss";

function SectionTechnologie() {
    const [isVisible, setIsVisible] = useState(false);
    const sectionRef = useRef(null);

    useEffect(() => {
        const handleScroll = () => {
            if (sectionRef.current) {
                const top = sectionRef.current.getBoundingClientRect().top;
                const windowHeight = window.innerHeight;
                setIsVisible(top < windowHeight);
            }
        };

        window.addEventListener("scroll", handleScroll);
        return () => {
            window.removeEventListener("scroll", handleScroll);
        };
    }, []);

    return (
        <section id="container-technologie" ref={sectionRef} className={isVisible ? "visible" : ""}>
            <div id="container-image-computer">
                <div id="container-picture_computer">
                    <div id="border-computer_1">
                    <img src={PictureFuture} className="picture-border-computer_1" alt="image bordure image"></img>
                    <img src={PictureFuture} className="picture-border-computer_2" alt="image bordure image"></img>
                    </div>
                    <div id="border-computer_2">
                    <img src={PictureFuture} className="picture-border-computer_1" alt="image bordure image"></img>
                    <img src={PictureFuture} className="picture-border-computer_2" alt="image bordure image"></img>
                    </div>
                </div>
            </div>
            <div id="container-h4_h5-computer">
                <div id="container-texte-computer">
                    <h2>QUI JE SUIS</h2>
                    <h3> la croise de la jeunesse et de la passion, je suis un crateur de sites web dtermin  offrir des expriences numriques exceptionnelles  des tarifs abordables. Arm de technologies de pointe, mon objectif est de concevoir des sites web  la pointe de l'innovation, exploitant les dernires avances technologiques pour donner vie  vos ides. Grce  ma matrise des technologies avances, je peux raliser des sites web avec des designs varis, allant de l'lgance minimaliste  l'clatante extravagance. Mon expertise s'tend galement au-del du web statique : je suis en constante formation pour matriser l'animation 2D et 3D, apportant ainsi une dimension dynamique et immersive  chaque projet. La scurit est une priorit absolue dans mon travail. Conscient des enjeux croissants lis  la protection des donnes et  la confidentialit en ligne, je m'engage  intgrer des mesures de scurit robustes  chaque tape du processus de dveloppement. Vos donnes et celles de vos utilisateurs sont entre de bonnes mains. En choisissant mes services, vous optez pour bien plus qu'un simple site web : vous investissez dans une exprience numrique de qualit, conue avec passion, expertise et engagement. Ensemble, crons l'avenir du web, o l'innovation et la crativit se rencontrent pour inspirer et captiver.</h3>
                </div>
            </div>
        </section>
    );
}

export default SectionTechnologie;