import React, { useEffect, useState } from 'react';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import Model3d from "../../../models-3d/panneau-portfolio.glb";
import "../../Header/Header.scss";

function Portfolio3D(){
    const [width, setWidth] = useState(130);
    const [height, setHeight] = useState(92);
    let renderer, scene, camera, mixer;

    useEffect(() => {
        const handleResize = () => {
            let newWidth = 130;
            if (window.innerWidth <= 1440) {
                newWidth = 100;
            }
            if (window.innerWidth <= 1122) {
                newWidth = 70;
            }
            setWidth(newWidth);
            setHeight(92);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    useEffect(() => {
        // Crer la scne Three.js
        scene = new THREE.Scene();

        // Crer une camra avec un champ de vision rduit
        camera = new THREE.PerspectiveCamera(30, width / height, 0.1, 1000);
        camera.position.set(9, 0, 0); // Dcaler lgrement la camra vers la gauche
        camera.lookAt(5, 0, -0.2); // Orienter la camra vers l'origine (0, 0, 0)

        // Dfinir la taille de rendu
        renderer = new THREE.WebGLRenderer();
        renderer.setSize(width, height);
        document.getElementById('container-header').appendChild(renderer.domElement);

        // Ajouter une lumire ambiante pour clairer toute la scne
        const ambientLight = new THREE.AmbientLight(0xffffff, 1.5); // Intensit  50%
        scene.add(ambientLight);

        // Ajouter une lumire directionnelle pour simuler une source de lumire principale
        const directionalLight = new THREE.DirectionalLight(0xffffff, 1.5); // Lumire blanche  pleine intensit
        directionalLight.position.set(70, 35, 3.5); // Position de la lumire
        scene.add(directionalLight);

        // Charger le modle glTF avec animation
        const loader = new GLTFLoader();
        loader.load(`${Model3d}`, function (gltf) {
            const model = gltf.scene;
            scene.add(model);
            
            // Obtenir toutes les animations du modle
            const animations = gltf.animations;

            // Crer un mlangeur d'animation
            mixer = new THREE.AnimationMixer(model);

            // Jouer toutes les animations disponibles
            animations.forEach(animation => {
                const action = mixer.clipAction(animation);
                action.play(); // Jouer l'animation

                // Arrter l'animation une fois termine
                action.clampWhenFinished = true;
                action.loop = THREE.LoopOnce;
            });

            // Boucle de rendu pour mettre  jour toutes les animations
            function animate() {
                requestAnimationFrame(animate);

                // Mettre  jour le rendu de la scne
                if (mixer) mixer.update(0.01); // Mettre  jour toutes les animations
                renderer.render(scene, camera);
            }

            animate();
        });

        // Fonction de nettoyage
        return () => {
            // Nettoyer les ressources si ncessaire
            // Par exemple, vous pouvez supprimer le renderer.domElement du document.body
            // et arrter la boucle de rendu de Three.js
            if (document.getElementById('container-header')) {
                document.getElementById('container-header').removeChild(renderer.domElement);
                renderer.dispose();
            }
            // Autres oprations de nettoyage si ncessaire
        };
    }, [width, height]);

    return (
        <div id="container-header"></div>
    );
}

export default Portfolio3D;